<template>
  <div id="app">
    <!-- Containers -->
    <div style="display: flex">
      <!-- Holds the SVG -->
      <div id="plot-container"></div>

      <!-- Controls the SVG-->
      <div class="grid-container">
        <button style="grid-column: 1 / 3" id="reset-btn">Reset</button>
        <!-- <input style="grid-column: 2 / 3; place-self: center" type="file" id="file-input" /> -->

        <span style="grid-column: 1; align-self: end; justify-self: center">X min</span>
        <span style="grid-column: 2; align-self: end; justify-self: center">X max</span>

        <input style="grid-column: 1" type="text" id="x-min-input" />
        <input style="grid-column: 2" type="text" id="x-max-input" />

        <span style="grid-column: 1; align-self: end; justify-self: center">Y min</span>
        <span style="grid-column: 2; align-self: end; justify-self: center">Y max</span>

        <input style="grid-column: 1" type="text" id="y-min-input" />
        <input style="grid-column: 2" type="text" id="y-max-input" />

        <span style="grid-column: 1 / 3; align-self: end; justify-self: center">Cylinders:</span>
        <span style="grid-column: 1 / 3; justify-self: center" id="output">No file loaded.</span>
      </div>
    </div>

    <!-- Modal -->
    <div v-if="showModal">
      <transition name="modal">
        <div class="modal-mask">
          <div class="modal-wrapper">
            <div class="modal-container">
              <div class="modal-header">Instructions</div>
              <div class="modal-body">
                <span>1. Load a geochemistry file with `Latitude_rel_cm` and `Longitude_rel_cm` columns</span>
                <br /><br />
                <span>2. Click and drag on the plot to zoom in on the highlighted region.</span>
                <br /><br />
                <span>3. The currently visible cylinders will be displayed in a list to the right of the plot.</span>
                <br /><br />
                <span>4. Hover on a dot in the plot to see the cylinder name.</span>
                <br /><br />
                <span>5. Click reset to return the plot to it's original axis limits.</span>
              </div>
              <div class="modal-footer">
                <button @click="showModal = false">OK</button>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import * as csv from "jquery-csv";
import * as d3 from "d3";

export default {
  name: "app",
  data: function () {
    return {
      showModal: true,
      width: window.innerWidth - 24,
      height: window.innerHeight - 60,
      svg: null,
      plotMargins: undefined,
      plotWidth: undefined,
      plotHeight: undefined,
      plotGroup: undefined,
      marksGroup: undefined,
      xScale: undefined,
      xAxis: undefined,
      xAxisGroup: undefined,
      yScale: undefined,
      yAxis: undefined,
      yAxisGroup: undefined,
      xGridlinesGroup: undefined,
      yGridlinesGroup: undefined,
      points: [],
      extent: {
        x: [0, 100],
        y: [0, 100],
      },
      filter: {
        x: [0, 100],
        y: [0, 100],
      },
    };
  },
  mounted: function () {
    let vm = this;

    const svgWidth = vm.height;
    const svgHeight = vm.height;

    // set plot margins, width and height
    vm.plotMargins = { top: 20, bottom: 50, left: 80, right: 20 };
    vm.plotWidth = svgWidth - vm.plotMargins.left - vm.plotMargins.right;
    vm.plotHeight = svgHeight - vm.plotMargins.top - vm.plotMargins.bottom;

    // add svg element
    vm.svg = d3
      .select("#plot-container")
      .append("svg")
      .attr("id", "svg")
      .attr("width", svgWidth)
      .attr("height", svgHeight);

    // Add plot group, containing axes, marks, and legend
    vm.plotGroup = vm.svg
      .append("g")
      .classed("plot", true)
      .attr("transform", `translate(${vm.plotMargins.left},${vm.plotMargins.top})`);

    // Add X and Y axis groups
    vm.yAxisGroup = vm.plotGroup.append("g").classed("y", true).classed("axis", true);
    vm.xAxisGroup = vm.plotGroup
      .append("g")
      .classed("x", true)
      .classed("axis", true)
      .attr("transform", `translate(${0},${vm.plotHeight})`);

    // add gridlines groups
    vm.xGridlinesGroup = vm.plotGroup.append("g").classed("x-gridlines", true);
    vm.yGridlinesGroup = vm.plotGroup.append("g").classed("y-gridlines", true);

    // Add data marks group
    vm.marksGroup = vm.plotGroup.append("g").classed("marks", true);

    // Add drag region to marks group
    let dragRegion = vm.marksGroup
      .append("rect")
      .attr("width", vm.plotWidth)
      .attr("height", vm.plotHeight)
      .style("opacity", 0);

    let selectionRect = {
      element: null,
      previousElement: null,
      currentY: 0,
      currentX: 0,
      originX: 0,
      originY: 0,
      init: function (newX, newY) {
        let rectElement = vm.marksGroup
          .append("rect")
          .attr("x", 0)
          .attr("y", 0)
          .attr("width", 0)
          .attr("height", 0)
          .attr("rx", 4)
          .attr("ry", 4)
          .attr("stroke", "#545454")
          .attr("stroke-width", "2px")
          .attr("stroke-opacity", "1")
          .attr("fill", "white")
          .attr("fill-opacity", "0.5")
          .classed("selection", true);
        this.setElement(rectElement);
        this.originX = newX;
        this.originY = newY;
        this.update(newX, newY);
      },
      update: function (newX, newY) {
        this.currentX = newX;
        this.currentY = newY;
        let x = this.currentX < this.originX ? this.currentX : this.originX;
        let y = this.currentY < this.originY ? this.currentY : this.originY;
        let width = Math.abs(this.currentX - this.originX);
        let height = Math.abs(this.currentY - this.originY);
        this.element.attr("x", x).attr("y", y).attr("width", width).attr("height", height);
      },
      setElement: function (ele) {
        this.previousElement = this.element;
        this.element = ele;
      },
      remove: function () {
        this.element.remove();
        this.element = null;
      },
      removePrevious: function () {
        if (this.previousElement) {
          this.previousElement.remove();
        }
      },
      getCurrentAttributes: function () {
        let x = +this.element.attr("x");
        let y = +this.element.attr("y");
        let width = +this.element.attr("width");
        let height = +this.element.attr("height");
        return {
          x1: x,
          y1: y,
          x2: x + width,
          y2: y + height,
        };
      },
    };

    let dragStart = function (e) {
      selectionRect.init(e.x, e.y);
      selectionRect.removePrevious();
    };

    let dragMove = function (e) {
      let newX = e.x < 0 ? 0 : e.x > vm.plotWidth ? vm.plotWidth : e.x;
      let newY = e.y < 0 ? 0 : e.y > vm.plotHeight ? vm.plotHeight : e.y;
      selectionRect.update(newX, newY);
    };

    let dragEnd = function () {
      let attrs = selectionRect.getCurrentAttributes();
      selectionRect.remove();
      if (attrs.x2 - attrs.x1 > 1 && attrs.y2 - attrs.y1 > 1) {
        vm.filter["x"][0] = vm.xScale.invert(attrs.x1);
        vm.filter["x"][1] = vm.xScale.invert(attrs.x2);
        vm.filter["y"][0] = vm.yScale.invert(attrs.y2);
        vm.filter["y"][1] = vm.yScale.invert(attrs.y1);
        document.getElementById("x-min-input").value = vm.xScale.invert(attrs.x1);
        document.getElementById("x-max-input").value = vm.xScale.invert(attrs.x2);
        document.getElementById("y-min-input").value = vm.yScale.invert(attrs.y2);
        document.getElementById("y-max-input").value = vm.yScale.invert(attrs.y1);
        vm.draw(); // update the vis
      }
    };

    let dragBehavior = d3.drag().on("start", dragStart).on("drag", dragMove).on("end", dragEnd);

    dragRegion.call(dragBehavior);

    // set the event listeners
    // document.getElementById("file-input").addEventListener("change", vm.readSingleFile, false);
    document.getElementById("reset-btn").addEventListener("click", vm.resetPointsFilter, false);
    document.getElementById("x-min-input").addEventListener("change", vm.setPointsFilter, false);
    document.getElementById("x-max-input").addEventListener("change", vm.setPointsFilter, false);
    document.getElementById("y-min-input").addEventListener("change", vm.setPointsFilter, false);
    document.getElementById("y-max-input").addEventListener("change", vm.setPointsFilter, false);

    // update inputs
    document.getElementById("x-min-input").value = vm.filter["x"][0];
    document.getElementById("x-max-input").value = vm.filter["x"][1];
    document.getElementById("y-min-input").value = vm.filter["y"][0];
    document.getElementById("y-max-input").value = vm.filter["y"][1];

    // load data
    vm.loadFile("Pescadero_GeoChem.csv").then(vm.loadData);
  },
  methods: {
    async loadFile(url) {
      const res = await fetch(url);
      return res.text();
    },

    resetPointsFilter() {
      let vm = this;
      vm.filter["x"][0] = vm.extent["x"][0];
      vm.filter["x"][1] = vm.extent["x"][1];
      vm.filter["y"][0] = vm.extent["y"][0];
      vm.filter["y"][1] = vm.extent["y"][1];
      document.getElementById("x-min-input").value = vm.extent["x"][0];
      document.getElementById("x-max-input").value = vm.extent["x"][1];
      document.getElementById("y-min-input").value = vm.extent["y"][0];
      document.getElementById("y-max-input").value = vm.extent["y"][1];
      vm.draw();
    },

    setPointsFilter() {
      let vm = this;
      // x-min
      vm.filter["x"][0] = document.getElementById("x-min-input").value;
      if (vm.filter["x"][0] < vm.extent["x"][0]) vm.filter["x"][0] = vm.extent["x"][0];
      if (vm.filter["x"][0] > vm.filter["x"][1]) vm.filter["x"][0] = vm.filter["x"][1] - 1;
      document.getElementById("x-min-input").value = vm.filter["x"][0];
      // x-max
      vm.filter["x"][1] = document.getElementById("x-max-input").value;
      if (vm.filter["x"][1] < vm.filter["x"][0]) vm.filter["x"][1] = vm.filter["x"][0] + 1;
      if (vm.filter["x"][1] > vm.extent["x"][1]) vm.filter["x"][1] = vm.extent["x"][1];
      document.getElementById("x-max-input").value = vm.filter["x"][1];
      // y-min
      vm.filter["y"][0] = document.getElementById("y-min-input").value;
      if (vm.filter["y"][0] < vm.extent["y"][0]) vm.filter["y"][0] = vm.extent["y"][0];
      if (vm.filter["y"][0] > vm.filter["y"][1]) vm.filter["y"][0] = vm.filter["y"][1] - 1;
      document.getElementById("y-min-input").value = vm.filter["y"][0];
      // y-max
      vm.filter["y"][1] = document.getElementById("y-max-input").value;
      if (vm.filter["y"][1] < vm.filter["y"][0]) vm.filter["y"][1] = vm.filter["y"][0] + 1;
      if (vm.filter["y"][1] > vm.extent["y"][1]) vm.filter["y"][1] = vm.extent["y"][1];
      document.getElementById("y-max-input").value = vm.filter["y"][1];
      // re-draw the vis
      vm.draw();
    },

    readSingleFile(e) {
      let vm = this;
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      let reader = new FileReader();
      reader.onload = function (e) {
        const contents = e.target.result;
        // read the csv
        csv.toObjects(contents, {}, (err, data) => {
          if (err) {
            console.log(err);
          }
          for (let i = 0, len = data.length; i < len; i++) {
            vm.filter["x"][0] = Math.min(vm.filter["x"][0], data[i]["Longitude_rel_cm"]);
            vm.filter["x"][1] = Math.max(vm.filter["x"][1], data[i]["Longitude_rel_cm"]);
            vm.filter["y"][0] = Math.min(vm.filter["y"][0], data[i]["Latitude_rel_cm"]);
            vm.filter["y"][1] = Math.max(vm.filter["y"][1], data[i]["Latitude_rel_cm"]);
            vm.points.push(data[i]);
          }
        });
        // update inputs and extent
        vm.extent["x"][0] = vm.filter["x"][0];
        vm.extent["x"][1] = vm.filter["x"][1];
        vm.extent["y"][0] = vm.filter["y"][0];
        vm.extent["y"][1] = vm.filter["y"][1];
        document.getElementById("x-min-input").value = vm.filter["x"][0];
        document.getElementById("x-max-input").value = vm.filter["x"][1];
        document.getElementById("y-min-input").value = vm.filter["y"][0];
        document.getElementById("y-max-input").value = vm.filter["y"][1];
        // draw the vis
        vm.draw();
      };
      reader.readAsText(file);
    },

    loadData(contents) {
      let vm = this;
      // read the csv
      csv.toObjects(contents, {}, (err, data) => {
        if (err) {
          console.log(err);
        }
        for (let i = 0, len = data.length; i < len; i++) {
          vm.filter["x"][0] = Math.min(vm.filter["x"][0], data[i]["Longitude_rel_cm"]);
          vm.filter["x"][1] = Math.max(vm.filter["x"][1], data[i]["Longitude_rel_cm"]);
          vm.filter["y"][0] = Math.min(vm.filter["y"][0], data[i]["Latitude_rel_cm"]);
          vm.filter["y"][1] = Math.max(vm.filter["y"][1], data[i]["Latitude_rel_cm"]);
          vm.points.push(data[i]);
        }
      });
      // update inputs and extent
      vm.extent["x"][0] = vm.filter["x"][0];
      vm.extent["x"][1] = vm.filter["x"][1];
      vm.extent["y"][0] = vm.filter["y"][0];
      vm.extent["y"][1] = vm.filter["y"][1];
      document.getElementById("x-min-input").value = vm.filter["x"][0];
      document.getElementById("x-max-input").value = vm.filter["x"][1];
      document.getElementById("y-min-input").value = vm.filter["y"][0];
      document.getElementById("y-max-input").value = vm.filter["y"][1];
      // draw the vis
      vm.draw();
    },

    draw() {
      let vm = this;

      if (!vm.points) return; // if there's no dataset don't update the scatter plot

      // create list of data points
      let rawData = vm.points.map((dataPoint) => {
        return {
          ...dataPoint,
          xVar: dataPoint["Longitude_rel_cm"],
          yVar: dataPoint["Latitude_rel_cm"],
        };
      });

      // filter list of data points
      let prepared = rawData.filter((dataPoint) => {
        return (
          dataPoint["Longitude_rel_cm"] >= vm.filter["x"][0] &&
          dataPoint["Longitude_rel_cm"] <= vm.filter["x"][1] &&
          dataPoint["Latitude_rel_cm"] >= vm.filter["y"][0] &&
          dataPoint["Latitude_rel_cm"] <= vm.filter["y"][1]
        );
      });

      // get unique cylinder names plotted
      let unique = [...new Set(prepared.map((d) => d.Cylinder))];
      if (unique.length > 0) {
        document.getElementById("output").innerHTML = `[
          ${unique.map((name) => `"${name}"`).join(",\n  ")}\n
        ]`;
      } else {
        document.getElementById("output").innerHTML = "None visible. To zoom out, press the Reset button.";
      }

      // Create scales and axes based on vis matrix
      let xAxisTitle = "Relative Longitude (cm)";
      let yAxisTitle = "Relative Latitude (cm)";

      // set scales
      vm.xScale = d3.scaleLinear().domain(vm.filter["x"]).range([0, vm.plotWidth]);
      vm.yScale = d3.scaleLinear().domain(vm.filter["y"]).range([vm.plotHeight, 0]);

      // set axes
      vm.xAxis = d3.axisBottom(vm.xScale);
      vm.yAxis = d3.axisLeft(vm.yScale);

      // draw axes
      vm.xAxisGroup.call(vm.xAxis);
      vm.yAxisGroup.call(vm.yAxis);

      // draw axis titles
      vm.xAxisGroup.select(".x.axis.title").remove();
      vm.xAxisGroup
        .append("g")
        .classed("x axis title", true)
        .attr("opacity", 1)
        .attr("transform", `translate(${vm.plotWidth / 2}, 0)`)
        .append("text")
        .attr("text-anchor", "middle")
        .attr("fill", "currentColor")
        .attr("dy", "3.71em")
        .text(xAxisTitle);
      vm.yAxisGroup.select(".y.axis.title").remove();
      vm.yAxisGroup
        .append("g")
        .classed("y axis title", true)
        .attr("opacity", 1)
        .attr("transform", `translate(-30, ${vm.plotHeight / 2})`)
        .append("text")
        .attr("fill", "currentColor")
        .text(yAxisTitle);

      // prepare data labels for yAxis
      vm.yAxisGroup
        .selectAll("text")
        .style("text-anchor", "middle")
        .attr("dx", "0.8em")
        .attr("dy", "-1.21em")
        .attr("transform", "rotate(-90)");

      // stagger every other tick label
      vm.xAxisGroup.selectAll(".tick").each(function (_, i) {
        if (i % 2 !== 0) {
          d3.select(this).select("line").attr("y2", 15);
          d3.select(this).select("text").attr("dy", "1.91em");
        }
      });
      vm.yAxisGroup.selectAll(".tick").each(function (_, i) {
        if (i % 2 !== 0) {
          d3.select(this).select("line").attr("x2", -15);
          d3.select(this).select("text").attr("dy", "-2.41em");
        }
      });

      // add gridlines
      let xGridlines = d3.axisTop().tickFormat("").tickSize(-vm.plotHeight).scale(vm.xScale);
      vm.xGridlinesGroup.select("*").remove();
      vm.xGridlinesGroup.call(xGridlines).call((g) => g.select(".domain").remove());
      let yGridlines = d3.axisRight().tickFormat("").tickSize(vm.plotWidth).scale(vm.yScale);
      vm.yGridlinesGroup.select("*").remove();
      vm.yGridlinesGroup.call(yGridlines).call((g) => g.select(".domain").remove());

      // Define the div for the tooltip
      let div = d3.select("body").append("div").attr("class", "tooltip").style("opacity", 0);

      // JOIN data selection using Primary Key label
      let dataBound = vm.marksGroup.selectAll(".post").data(prepared);

      // DELETE extra points
      dataBound.exit().remove();

      // ENTER new points (starting invisible, later fade them in)
      let enterSelection = dataBound.enter().append("g").classed("post", true);

      // UPDATE all existing points
      enterSelection.append("circle");
      enterSelection
        .merge(dataBound)
        .select("circle")
        .attr("transform", (d) => `translate(${vm.xScale(d["xVar"])},${vm.yScale(d["yVar"])})`)
        .attr("r", 6)
        .style("fill", "white")
        .style("fill-opacity", 0.8)
        .style("stroke-width", "1px")
        .style("stroke", "black")
        .style("cursor", "pointer")
        .on("mouseover", function (e, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(d.Cylinder)
            .style("left", e.x + "px")
            .style("top", e.y - 28 + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });
    },
  },
};
</script>

<style>
#plot-container {
  margin-right: 1rem;
  border: 4px solid lightgray;
  border-radius: 4px;
}

.grid-container {
  display: grid;
  gap: 10px 10px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: repeat(auto-fit, minmax(20px, 30px));
  justify-items: stretch;
  align-items: stretch;
}

.x-gridlines line,
.y-gridlines line {
  stroke: #ddd;
}

.tooltip {
  position: absolute;
  text-align: center;
  width: 80px;
  height: 15px;
  padding: 2px;
  font: 12px sans-serif;
  background: lightsteelblue;
  border: 0px;
  border-radius: 8px;
  pointer-events: none;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 500px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  margin-top: 0;
  font-size: 1.2rem;
  font-weight: 800;
  color: #42b983;
}

.modal-body {
  font-size: 0.8rem;
  margin: 20px 0;
}

.modal-footer {
  text-align: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
